import * as React from "react";
import styled from "styled-components";

const Img = styled.img`
  width: 160px;
  height: 26px;
  margin-left: 30px;
  src: ${props => props.src};
`;

interface ILogoProps {
  src?: any;
  // src?: HTMLImageElement | string | File;
}

const Logo: React.SFC<ILogoProps> = props => {
  return (
    <a href="https://www.naver.com">
      <Img {...props} />
      {/* <Img src={props.src} /> */}
    </a>
  );
};

export default Logo;



