import styled from "styled-components";

const HeaderContainer = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 38px;
  min-height: 60px;
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.08);
`;

export default HeaderContainer;



